.message{
    border-radius: 5px;
    padding: 5px 10px;
    margin: 0;
    margin: 1em auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #000;
    width: max-content;
}

.error{
    color: #f21c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
}

.success{
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
}